.header {
  z-index: 100;
  background-image: url("header-background-mobile.a8ceff09.jpg");
  background-repeat: no-repeat;
  background-size: 100vw 100%;
  justify-content: center;
  align-items: center;
  width: 100%;
  height: 125px;
  font-size: .875rem;
  transition: all .3s ease-in-out;
  display: flex;
  position: fixed;
  top: 0;
}

.header__container {
  justify-content: space-between;
  align-items: center;
  width: 100%;
  transition: all .5s ease-in-out;
  display: flex;
}

.header__nav {
  flex-direction: column;
  justify-content: space-between;
  align-items: center;
  width: 100%;
  padding: 0 20px;
  transition: all .5s ease-in-out;
  display: flex;
}

.header__nav-container {
  justify-content: space-between;
  align-items: center;
  width: 100%;
  height: 100%;
  display: flex;
}

.header__nav-icon {
  stroke: #f5f5f5;
  width: 20px;
  height: 20px;
}

.header__nav-icon:hover {
  stroke: #ff6b08;
}

.header__nav-title {
  display: none;
}

.header__nav-list {
  justify-content: space-between;
  align-items: center;
  width: 100%;
  display: flex;
}

.header__nav-list > ul {
  justify-content: center;
  align-items: center;
  gap: 40px;
  display: flex;
}

.header__nav-language-btn {
  color: #f5f5f5;
  text-transform: uppercase;
  border: 1px solid;
  border-radius: 5px;
  justify-content: center;
  align-items: center;
  padding: 2px;
  transition: all .3s ease-in-out;
  display: flex;
}

.header__nav-language-btn:hover, .header__nav-language-btn:focus {
  filter: drop-shadow(0 8px 43px #ff6b0199);
  background: #ff6b08;
}

.header__nav-form {
  border-bottom: 1px solid #ff6b08;
  justify-content: center;
  align-items: center;
  gap: 3px;
  width: 280px;
  margin-top: 10px;
  display: flex;
}

.header__nav-input {
  color: #f5f5f5;
  background: none;
  border: none;
  outline: none;
  width: 100%;
  padding: 4px;
}

.header__nav-input::placeholder {
  color: #f5f5f5;
}

.header__nav-input:focus + .header__nav-input-submit svg, .header__nav-input:hover + .header__nav-input-submit svg {
  stroke: #ff6b08;
  color: #f5f5f5;
}

.header__nav-input:focus::placeholder {
  color: #ff6b08;
}

.header__nav-input--loupe {
  cursor: pointer;
  stroke: #f5f5f5;
  transition: all .3s ease-in-out;
}

.header__error {
  color: #ff001b;
  white-space: nowrap;
  text-align: center;
  font-size: .75rem;
  display: none;
}

.header--library {
  background-image: url("my-library-mobile.f70bf307.jpg");
}

.header--library__btn--container > button {
  border: 1px solid getcolor("header");
  cursor: pointer;
  text-transform: uppercase;
  background: none;
  border-radius: 5px;
  width: 130px;
  height: 44px;
  margin-top: 20px;
}

.header--library__btn--container > button:first-of-type {
  margin-right: 15px;
}

.header--library__btn--container--active {
  background: getcolor("orange") !important;
}

.underline {
  border-bottom: 2px solid #ff6b08;
}

.header.scrolled {
  height: 40px;
  transition: all .5s ease-in-out;
}

.scrolled-container {
  height: 67px;
}

.hovered {
  transition: all .5s ease-in-out;
  height: 125px !important;
}

.form-transform {
  opacity: 0;
  pointer-events: none;
  z-index: -10;
  margin: 0;
  transition: all 10ms ease-in-out;
  transform: translateY(-100%);
}

.nav-height {
  padding-top: 40px;
  transition: all .5s ease-in-out;
  position: relative;
}

.buttons-transform {
  opacity: 0;
  pointer-events: none;
  z-index: -10;
  margin: 0;
  transition: all 10ms ease-in-out;
  transform: translateY(-100%);
}

@media screen and (width >= 768px) {
  .header {
    background-image: url("header-background-tablet.3b543a43.jpg");
    height: 108px;
  }

  .header__nav {
    font-size: 1.25rem;
  }

  .header__nav-title {
    display: block;
  }

  .header__nav-list {
    justify-content: space-between;
    align-items: center;
    width: 100%;
    display: flex;
  }

  .header__nav-icon {
    width: 32px;
    height: 32px;
  }

  .header__nav-link {
    align-items: center;
    display: flex;
  }

  .header__nav-link:hover > svg {
    stroke: #ff6b08;
  }

  .header__nav-link > svg {
    margin-top: 5px;
  }

  .header__nav-form {
    margin-bottom: 10px;
  }

  .header__nav-input--loupe {
    width: 16px;
    height: 16px;
  }

  .header-form {
    margin: 0;
  }
}

.hovered {
  height: 108px !important;
}

@media screen and (width >= 1280px) {
  .header__nav-form {
    width: 394px;
  }

  .header__error {
    width: 393px;
  }
}

